import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import styles from './Navbar.module.css';
import { Link } from 'react-router-dom';
import logo from '../../images/freshcart-logo.svg';
import { carteContext } from '../../Context/CartContext';
import Wishlist from './../Wishlist/Wishlist';



export default function Navbar({ userdata, logout }) {


    let { displayCart, displayWishlist } = useContext(carteContext)
    return <>
        <nav className="navbar position-fixed fixed-top navbar-expand-sm navbar-light bg-light">
            <div className="container">
                <Link className="navbar-brand" to="/"><img src={logo} alt="" /></Link>
                <button className="navbar-toggler d-lg-none" type="button" data-bs-toggle="collapse" data-bs-target="#collapsibleNavId" aria-controls="collapsibleNavId"
                    aria-expanded="false" aria-label="Toggle navigation">
                    <i class="fa-solid fa-minimize fa-xl py-3"></i>
                </button>
                <div className="collapse navbar-collapse" id="collapsibleNavId">
                    {userdata !== null ? <ul className="navbar-nav me-auto mt-2 mt-lg-0">

                        <li className="nav-item">
                            <Link className="nav-link" to="/">Home</Link>
                        </li>

                        <li className="nav-item">
                            <Link className="nav-link" to="Products">Products</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="Categories">Categories</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="brands">Brands</Link>
                        </li>

                    </ul> : null}


                    <ul className="navbar-nav ms-auto mt-2 mt-lg-0">

                        {userdata == null ? <>

                            <li className="nav-item">
                                <Link className="nav-link" to="Login">Login <i class="fa-solid fa-right-to-bracket mx-2"></i></Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="Register">Register <i class="fa-solid fa-user-plus mx-2"></i></Link>
                            </li>
                        </> : <>

                            <li className="nav-item">
                                <Link className="nav-link " to="cart"> Cart  <i className='fas fa-shopping-cart fa-lg'></i><span className='badge bg-main text-white mx-1'>{displayCart}</span></Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="wishlist"> Wishlist <i className='fa-regular fa-heart fa-lg'></i><span className='badge bg-main text-white mx-1'>{displayWishlist}</span></Link>
                            </li>
                            <li className="nav-item">
                                <span onClick={logout} className="cursor-pointer nav-link" >Logout <i class="fa fa-sign-out mx-1" aria-hidden="true"></i></span>
                            </li>
                        </>}





                    </ul>

                </div>
            </div>
        </nav>


    </>
}


