import React, { useContext, useState, useEffect } from 'react';
import { carteContext } from '../../Context/CartContext';
import { toast } from 'react-hot-toast';


export default function Wishlist() {


    const [WishlistDetails, setWishlistDetails] = useState(null);
    let { getLoggedUserWishlist, removewishlist, addToCart, setdisplayCart, setDisplayWishlist } = useContext(carteContext);
    async function getWishlist() {
        let response = await getLoggedUserWishlist()
        
        if (response?.data?.status == 'success') {
            setWishlistDetails(response?.data?.data)

        }
    }
    async function addProducts(product_id) {
        let response = await addToCart(product_id);

        if (response?.data?.status == 'success') {
            setdisplayCart(response?.data?.numOfCartItems)
            toast.success(response?.data?.message, { className: 'bg-main  text-white ', duration: 2000 })
        } else {
            toast.error(response?.response?.data?.message)
        }
    }
    async function deleteItem(productId) {
        let response = await removewishlist(productId);
       
        setDisplayWishlist(response?.data?.data.length)
        setWishlistDetails(response.data.data)
        console.log(response.data.data)
        getWishlist()
        toast.success('Item Removed From WishList !', { className: 'bg-main  text-white ', duration: 2000 })
    }

    useEffect(() => {
        getWishlist()
    }, [])

    return <>
        <div className="container">
            <div className="row text-center">
                <div className="col-md-12">
                    <p className='text-muted py-2 header-wish'> Home / Wishlist</p>
                </div>
                <div className="col-md-12">
                    <i class="fa-regular fa-heart wishlist"></i>
                </div>
                <div className="col-md-12 py-2">
                    <h3 className='fw-bolder'>My WishList</h3>
                </div>
            </div>
        </div>

        {WishlistDetails ?
            <div className='bg-main-light p-4 my-4 bg-light'>
                <table className="table  table-responsive table-responsive-lg ">
                    <thead>
                        <tr>
                            <th className="col-md-3 text-center">Product Name</th>
                            <th className="col-md-2 text-center">Price</th>
                            <th className="col-md-2 text-center">Stock Status</th>
                            <th className="col-md-2 text-center">Add to Cart</th>
                            <th className="col-md-2 text-center">Remove from Wishlist</th>
                        </tr>
                    </thead>
                    <tbody>
                        {WishlistDetails.map((product) =>
                            <tr key={product?._id}>
                                <td className="col-md-3">
                                    <div className='d-flex align-items-center me-4'>
                                        <img className='w-25 ' src={product?.imageCover} alt="" />
                                        <div>
                                            <h6 className="ms-4">{product?.title?.split(' ').slice(0, 2).join(' ')}</h6>

                                        </div>
                                    </div>
                                </td>
                                <td className="col-md-2 pt-4 text-center ">{product?.price} EGP</td>
                                <td className="col-md-2 pt-4 text-center">{product?.inStock ? "Yes" : 'In Stock'}</td>
                                <td className="col-md-2 ">
                                    <button onClick={() => addProducts(product?._id)} className="btn btn-lg w-100 add-button"><i className="fa-solid fa-circle-plus"></i></button>
                                </td>
                                <td className="col-md-2 ">
                                    <button onClick={() => deleteItem(product?._id)} className='btn btn-lg w-100  remove-button'><i class="fa-solid fa-trash-can"></i></button>
                                </td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
            : <p>Wishlist is empty.</p>}

    </>
}


