import React, { useEffect, useState , useContext } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import { carteContext } from '../../Context/CartContext.js';
import Slider from "react-slick";
import toast from 'react-hot-toast';


export default function ProductDetalis() {
    let { } = useContext(carteContext)
    const [productDetalis, setproductDetalis] = useState(null);
    const [isLoading, setisLoading] = useState(false);
    let { addToCart  , setdisplayCart} = useContext(carteContext)
    let params = useParams()


    async function addProducts(product_id) {
        let response = await addToCart(product_id);
        

        if (response?.data?.status == 'success') {
            setdisplayCart(response?.data?.numOfCartItems)
            toast.success(response?.data?.message, { className: 'bg-main  text-white ', duration: 1000 })
        }

        else {
            toast.error(response?.response?.data?.message)
        }
    }

    async function getProductDetails(id) {
        setisLoading(true);

        let { data } = await axios.get(`https://ecommerce.routemisr.com/api/v1/products/${id}`);
        setproductDetalis(data.data)
        setisLoading(false);


    }
    useEffect(() => {
        getProductDetails(params.id)

    }, [])
    var settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };

    
    return <>
    {isLoading? <div className="text-center"><i className='fas fa-2x fa-spin fa-spinner text-main py-5'></i></div>: 
     <div className='row py-3 align-items-center'>
     <div className='col-md-4 cursor-grab'>
         <Slider {...settings}>
             {productDetalis?.images.map((img) => <img className='w-100' src={img} alt="" />)}
         </Slider>

     </div>
     <div className='col-md-8'>
         <h3 className='py-2'>{productDetalis?.title}</h3>
         <p className='text-muted '>{productDetalis?.description}</p>
         <div className='d-flex justify-content-between'>
             <span className='text-muted py-3'>{productDetalis?.price} EGP</span>
             <span>
                 <i className='fas fa-star rating-color py-3'></i>
                 {productDetalis?.ratingsAverage}
             </span>

         </div>
         <button onClick={() => addProducts(params?.id)} className="btn bg-main w-100 text-white ">+ Add</button>
     </div>
 </div>
    }
       

    </>
}


