import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';

export default function CategoryDetails() {

    const [CategoryDetails, setCategoryDetails] = useState(null);
    const [isLoading, setisLoading] = useState(false);
    let params = useParams()

    async function getCategoriesDetails(id) {
        setisLoading(true);
        let { data } = await axios.get(`https://ecommerce.routemisr.com/api/v1/categories/${id}`);
        setCategoryDetails(data.data)
        setisLoading(false);   



    }   
    useEffect(() => {
        getCategoriesDetails(params.id)

    }, [])

    return <>
    {isLoading? <div className="text-center"><i className='fas fa-2x fa-spin fa-spinner text-main py-5'></i></div> :         <div className='row py-3 align-items-center'>
            <div className='col-md-9 m-auto text-center'>
                <h2 className='text-main py-2'>{CategoryDetails?.name}</h2>
                <img className='w-100' src={CategoryDetails?.image} alt="" />

            </div>

        </div>}


    </>
}


