import React from 'react';
import PropTypes from 'prop-types';
import styles from './Footer.module.css';

export default function Footer() {
    return <>
        <div className="container-fluid bg-light py-5 mt-5 ">
            <div className="row px-5  ">
                <div className="col-md-12">
                    <h4>Get the FreshCart app</h4>
                    <p className='text-muted' >We will send you a link , open it on your phone to download this app.</p>
                </div>
                <div className="col-md-12">
                    <div class="input-group mb-3">
                        <input type="text" class="form-control" placeholder="Email" aria-label="Email" aria-describedby="basic-addon2"></input>
                        <div class="input-group-append ps-4">
                            <button class="btn bg-main button-footer text-white  px-4" type="button">Share app link</button>
                        </div>
                    </div>
                    <hr className='text-muted'></hr>
                </div>

                <div className="col-md-12 py-2 mb-2">
                    <h5>Payment Partners <i className="fa-brands fa-amazon-pay px-1"></i> <i class="fa-brands fa-cc-amex px-1"> </i> <i class="fa-brands fa-cc-mastercard px-1"></i> <i class="fa-brands  px-1 fa-paypal"></i></h5>
                </div>
                <hr className='text-muted'></hr>

                <div className="row py-3 text-center">
                    <div className="col-md-12">
                        <p>Copyrights <i class="fa-regular fa-copyright"></i>, Reserverd for Hazem Zidan.</p>
                    </div>
                    <div className="col-md-12">
                        <li className="nav-item cursor-pointer text-center list-unstyled">
                            <i className="fab mx-2 fa-facebook"></i>
                            <i className="fab mx-2 fa-twitter"></i>
                            <i className="fab mx-2 fa-instagram"></i>
                            <i className="fab mx-2 fa-tiktok"></i>
                            <i className="fab mx-2 fa-linkedin"></i>
                            <i className="fab mx-2 fa-youtube"></i>
                        </li>
                    </div>
                </div>

            </div>
        </div>

    </>
}


