import React, { useContext, useState, useEffect } from 'react';
import { carteContext } from '../../Context/CartContext';
import { toast } from 'react-hot-toast';
import { Link } from 'react-router-dom';

export default function Cart() {

    const [cartDetails, setcartDetails] = useState(null);
    
    let { getLoggedUserCart, removeItem, updateProductCount, clearUserCart, setdisplayCart } = useContext(carteContext);
    async function getCart() {
        let response = await getLoggedUserCart()
        if (response?.data?.status == 'success') {
            setcartDetails(response.data.data)

        }
    }
    async function deleteItem(productId) {
        let response = await removeItem(productId);
        setdisplayCart(response?.data?.numOfCartItems)
        setcartDetails(response?.data?.data)
        toast.success('Product Removed', { className: 'bg-main  text-white ', duration: 2000 })
    }
    async function updateProductQuantity(productId, count) {
        if (count == 0) {
            deleteItem(productId);
        }
        let response = await updateProductCount(productId, count);
        setcartDetails(response.data.data)
        toast.success('Product Count Updated !', { className: 'bg-main  text-white ', duration: 2000 })
    }
    async function clearProduct() {
        let response = await clearUserCart();
        setdisplayCart(response?.data?.numOfCartItems)
        setcartDetails(response.data.data)
        toast.success('All Products Deleted Successfuly From Cart!', { className: 'bg-main  text-white ', duration: 2000 })
    }
    useEffect(() => {
        getCart()
    }, [])

    return <>
        <div className="container">
            <div className="row text-center">
                <div className="col-md-12">
                    <p className='text-muted py-2 header-wish'> Home / Cart</p>
                </div>
                <div className="col-md-12">
                    <i class="fa-solid fa-cart-arrow-down wishlist"></i>
                </div>
                <div className="col-md-12 py-2">
                    <h3 className='fw-bolder'>My Shopping Cart</h3>
                </div>
            </div>
        </div>

        {cartDetails ?
            <div className='bg-main-light p-4 my-4 bg-light'>
                <table className="table  table-responsive table-responsive-lg">
                    <thead className='text-muted'>
                        <tr>
                            <th className="col-md-3 text-center">Product Name</th>
                            <th className="col-md-2 text-center">Quantity</th>
                            <th className="col-md-2 text-center">Remove</th>
                            <th className="col-md-2 text-center">Price</th>
                        </tr>
                    </thead>
                    <tbody>
                        {cartDetails.products.map((product) => (
                            <tr key={product.product?._id}>
                                <td className="col-md-3">
                                    <div className='d-flex align-items-center me-4'>
                                        <img className='w-25 me-4' src={product.product?.imageCover} alt="" />
                                        <div>
                                            <h6 className="ms-4">{product.product?.title?.split(' ').slice(0, 2).join(' ')}</h6>
                                        </div>
                                    </div>
                                </td>
                                <td>


                                    <div className="col-md-12  ">
                                        <div className="d-flex justify-content-center align-items-center">
                                            <button onClick={() => updateProductQuantity(product.product?._id, product.count + 1)} className='btn  btn-lg w-25 add-button  rounded-circle  '><i className="fa-solid fa-circle-plus"></i></button>
                                            <span className='px-4 pe-1'>{product.count}</span>
                                            <button onClick={() => updateProductQuantity(product.product?._id, product.count - 1)} className='btn btn-lg  w-25 remove-button  rounded-circle '><i className="fa-solid fa-circle-minus"></i></button>
                                        </div>
                                    </div>

                                </td>


                                <td className="col-md-2 ">
                                    <button onClick={() => deleteItem(product?._id)} className='btn btn-lg w-100  remove-button'><i class="fa-solid fa-trash-can"></i></button>
                                </td>
                                <td className="col-md-2 pt-4 text-center ">{product?.price} EGP</td>
                            </tr>
                        ))}
                        <tr>

                            <div className="col-md-12 ">
                                <td className=' fw-bold pt-4'>Summary: </td>
                            </div>
                            <div className="col-md-12">
                                <hr className='w-100 text-muted'></hr>
                            </div>
                            <div className="col-md-12">
                                <td className='text-muted'>Sub Total : {cartDetails.totalCartPrice} EGP.</td>
                            </div>
                            <div className="col-md-12">
                                <td className='text-muted'>Sales Taxs : 0%.</td>
                            </div>
                            <div className="col-md-12">
                                <td className='text-muted'>Shipping  : Calculate Above.</td>
                            </div>
                            <div className="col-md-12">
                                <hr className='w-100 text-muted'></hr>
                            </div>

                            <div className="col-md-12 float-end ">
                                <td className=" fw-bold">Total Cart Price: {cartDetails.totalCartPrice} EGP</td>

                            </div>



                        </tr>
                    </tbody>
                </table>

                <div className="row py-3">
                    <div className="col-md-6">
                        <button onClick={clearProduct} className='btn py-2  w-100 my-3 btn-clear '>Clear Cart <i className="fa-solid fa-trash-arrow-up ms-2"></i></button>
                    </div>
                    <div className="col-md-6">
                        <button className='btn  w-100 my-3 btn-checkout'>
                            <Link className='text-white py-1  w-100 d-block' to={'/checkout'}>
                                Checkout <i className="fa-solid fa-basket-shopping ms-2"></i>
                            </Link>
                        </button>
                    </div>
                </div>
            </div>
            : <p>Your cart is empty.</p>
        }
    </>
}


